
import Tab from 'vux/src/components/tab/tab.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'

import reportService from '../service/reportService'
import saleReportService from '../service/saleRepotService'

const PROJ_LIST = 'ROSE_PROJ_LIST'
const PAGE_SIZE = 30
export default {
components: {
Tab,
Cell,
Group,
TabItem,
Calendar,
CellFormPreview,
Grid,
GridItem,
PopupPicker,
Selector,
Divider
},
data() {
return {
showTab: true, // 是否展示日期列表
activeIndex: 0,
filterDate: [], // 日历的选中数据
reportList: [], // 当前展示的列表
curTab: 'days',
dateList: [
{
name: '本日',
value: 'days'
},
{
name: '本周',
value: 'weeks'
},
{
name: '本月',
value: 'months'
},
{
name: '本年',
value: 'years'
},
], // 日期列表
reportData: { // 获取的后台数据
days: [],
weeks: [],
months: [],
years: []
},
filterParams: {}, // 过滤条件
showDate: false, // 是否展示日期列表
dateSelected: { // 选中的日期
name: '本日',
value: 'days'
},
showProj: false, // 是否展示产品列表
projList: [], // 产品列表
page: 1, // 请求页码
curPage: 1, // 当前页码
objName: '', // 项目类型
isDisabled: false
}
},
methods: {
onItemClick(item) {
this.curTab = item.value
this.reportList = this.reportData[item.value]
},
onChange(val) {
console.log(val);

},

resetReportData() {
this.reportData = {
days: [],
weeks: [],
months: [],
years: []
}
},

assembleData(params = {}) {
reportService.getReport(Object.assign(this.filterParams, {
objName: this.objName,
pageNo: this.page
})).then(data => {
this.resetReportData()
let map = ['days', 'weeks', 'months', 'years']

map.forEach(item => {
data[item].forEach((data, index) => {
let detail = [
{
label: '项目类产品',
value: `${data.qty || 0}件/套`
},
{
label: 'A类产品',
value: `￥${numberComma(data.aProduct) || 0}`
},
/*{
label: 'B类产品',
value: '￥999'
}, */{
label: '所在部门',
value: data.bmName || ''
}, {
label: '所属银行',
value: data.bankName || ''
}
];
if (this.objName === '') {
detail.shift()
}
this.reportData[item].push({
name: `${(index + 1) + (this.page - 1) * PAGE_SIZE}. ${data.creator}`,
sales: '',
showContent: false,
detail: detail
})
})
})
this.curPage = this.page
this.reportList = this.reportData[this.dateSelected.value]
this.isDisabled = this.reportList.length < PAGE_SIZE
}).catch(err => {
this.resetReportData()
this.reportList = []
})
},

getProj() {
let proj = JSON.parse(sessionStorage.getItem(PROJ_LIST))
proj.shift()
this.projList = proj
},

hideDropList() {
this.showDate = false;
this.showProj = false;
},

dateClick() {
this.showDate = !this.showDate;
this.showProj = false;
},

dateItemClick(item) {
this.showDate = false;
this.dateSelected = item;
if (this.page === 1) {
this.reportList = this.reportData[item.value];
} else {
this.page = 1
this.assembleData()
}
},

projClick() {
this.showProj = !this.showProj;
this.showDate = false;
},

projItemClick(item) {
this.showProj = false;
this.objName = item;
this.page = 1
this.assembleData()
},

aProjClick() {
this.showProj = false;
this.showDate = false;
this.objName = '';
this.page = 1
this.assembleData();
},

pagePrev() {
if (this.page === 1) {
return
}
this.page--;
this.assembleData();
},

pageNext() {
if (this.isDisabled) {
return
}
this.page++;
this.assembleData();
},
},
created() {
let query = this.$route.query
console.log(query.proj)
this.filterParams = {
shengName: query.region || '', // 区域
bankName: query.bank || '', // 银行
bmName: query.dept || '', // 部门
objName: query.proj || '', // 项目
}

if (this.filterParams.objName) {
this.objName = this.filterParams.objName;
}
this.assembleData(this.filterParams)
this.getProj()
}
}



